<template>
  <div class="game">
    <header-illustration
      :title="header.title"
      :illustration="header.illustration"/>
    <main class="box">
      <p
        v-typeit="() => typeItDone = true"
        class="text">
        {{ header.text }}
      </p>
      <div
        v-if="typeItDone"
        class="actions">
        <button><ShareFacebook url="https://zhaoyingke.github.io/startup-nation/#/"/></button>
        <button><ShareTwitter url="https://zhaoyingke.github.io/startup-nation/#/"/></button>
        <button @click="gobackhome">Retourner  la page d'accueil</button>
      </div>
      <gauges/>
    </main>
  </div>
</template>

<script>
import Gauges from '@/components/Gauges'
import HeaderIllustration from '@/components/HeaderIllustration'
import { ShareFacebook, ShareTwitter } from 'vue-share-social'

export default {
  name: 'fin',
  components: {
    Gauges,
    HeaderIllustration,
    ShareFacebook,
    ShareTwitter
  },
  data () {
    return {
      typeItDone: false
    }
  },
  computed: {
    header () {
      if (this.$store.state.money > 1000) {
        return {
          title: 'Continuer comme tel...',
          illustration: 'Continue.png',
          text: `Tout comme la vtre, la plupart des startups continuent  s'agrandir, sans pourtant passer par une entre en bourse, ni se faire acheter par des joueurs majeurs dans le march. `
        }
      }

      if ((this.$store.state.investorPoints > 16) || (this.$store.getters.npv > 0)) {
        return {
          title: 'Introduction en bourse',
          illustration: 'ipo.png',
          text: `Flicitations ! Vos investisseurs vous accompagnent jusqu' l'introduction en bourse, une tape que trs peu d'entreprises naissantes ont atteinte.`
        }
      }

      if ((this.$store.state.reputationPoints > 16) || (this.$store.state.investorPoints > 10)) {
        return {
          title: `Vous faire acheter par un concurrent`,
          illustration: `meetinginvestors.png`,
          text: `Flicitations ! Vous venez de recevoir une offre de rachat par un concurrent majeur dans le march. L'acquisition n'est pas une dfaite, car vous jouez un rle important dans le secteur, ce qui pose une menace  l'gard des concurrents. Nombreux entrepreneurs, dont notamment votre conseiller Barthlemy Aupe, finissent par se faire acheter. Rester  juger si l'offre de rachat ira bien pour votre cration.`
        }
      } else {
        return {
          title: `Pas d'objectif atteint`,
          illustration: `failure.png`,
          text: `L'entreprenariat n'est jamais simple - mais vous n'tes pas seul.e. Le taux de faillite en France est de 40% pour une start-up non accompagne, et de 20% pour celles accompagnes. En tout cas, n'ayez pas peur: vouz pouvez toujours ressayer!`
        }
      }
    }
  },
  watch: {},
  methods: {
    gobackhome () {
      this.$router.push({
        name: 'home'
      })
    }
  }
}
</script>

